
import IGuest from '@/models/IGuest';
import IPlusOne from '@/models/IPlusOne';
import IRsvpResponse, { createPartyYesRsvp, createPartyNoRsvp } from '@/models/IRsvpResponse';
import { getGuestIds, getGuests } from '@/utilities/FirebaseUtils';
import { toTitleCase } from '@/utilities/utils';
import { Firestore } from 'firebase/firestore';
import { PropType } from 'vue';
import { Options, Vue } from 'vue-class-component';

@Options({
  props: {
    errorMessage: String,
    guestId: String,
    isInert: Boolean,
    partyId: String,
    previousRsvpResponse: Object as PropType<IRsvpResponse | null>,
    weddingDb: Object as PropType<Firestore>,
  },
})
export default class HomeView extends Vue {

  //#region PROPS

  /** Text eplaining any error messages that occred with the RSVP response. */
  errorMessage: string = '';

  /** the guest who logged in to fill out this RSVP, or the default guest in the
   * email hyperlink that brought the user to this page. */
  guestId!: string;

  /** When true, this entire form becomes inert (user cannot interact with it at
   * all) */
  isInert!: boolean;

  /** The guest party that this form relates too */
  partyId!: string

  /** This previously RSVP response sent by this party, if one exists. Should be
   * null if they haven't yet submitted an RSVP response. */
  previousRsvpResponse!: IRsvpResponse | null;

  /** The Firestore database containg the wedding data */
  weddingDb!: Firestore;

  //#endregion PROPS


  // #region DATA

  /** advice or jokes recommendations fromt he guest */
  adviceOrJokes: string = '';

  /** Whether this guest wants to bring a plus one (if we gave them that
   * option). Note: Uses the same 'yes/no' text as the 'canAttandChoice' */
  bringingPlusOne: string = '';

  /** The chosen radio button for if the user can attend or not. Will be an
   * empty string until they make a choice. */
  canAttendChoice: string[] = [];

  /** The text used in the 'can attend' radio button choice */
  canAttendText_yes: string = "Yes";

  /** The text used in the 'can not attend' radio button choice */
  canAttendText_no: string = "No";

  /** All the guests in this party */
  guests: IGuest[] = [];

  /** The first name of the plue one this guest is bringing, as entered into the text field */
  plusOneFirstName: string = '';

  /** The last name of the plue one this guest is bringing, as entered into the text field */
  plusOneLastName: string = '';

  /** reason the guest had to decline the invitation */
  reasonForDeclining: string = '';

  /** Song recommendations fromt he guest */
  songRecommendations: string = '';

  // #endregion DATA


  // #region COMPUTED

  /** Returns 'you' if there is only one guest (not including plus-ons), returns
   * y'all if there are multiple guests */
  get youOrYall(): string {
    return this.guests.length == 1 ? 'you' : "y'all"
  }

  /** true only if the submit button should be enabled (all required form fields
   * are filled out), otherwise false. */
  get formComplete(): boolean {
    const numOfGuestsRespondedFor: number = this.canAttendChoice.filter(x => x != '').length;
    const haveAllGuestsResponded: boolean = this.guests.length == numOfGuestsRespondedFor;
    const firstGuestNotComing: boolean = this.canAttendChoice[0] == this.canAttendText_no;
    const hasPlusOne: boolean = this.guests.length > 0 /** makes sure guests array exists */ && this.guests[0].hasPlusOne;
    const bringingPlusOne: boolean = this.bringingPlusOne == this.canAttendText_yes;
    const notBringingPlusOne: boolean = this.bringingPlusOne == this.canAttendText_no;
    const plusOneNameProvided: boolean = this.plusOneFirstName.length > 0 && this.plusOneLastName.length > 0;
    const plusOneSectionComplete: boolean =
      !hasPlusOne || // this party doesn't have a plus one, or
      firstGuestNotComing || // this guest is not coming, or
      (hasPlusOne && notBringingPlusOne) || // this guest has a plus one, but choose not to bring one, or
      (hasPlusOne && bringingPlusOne && plusOneNameProvided) // this guest has and is bringing a plus one, and has provided their first and last name
    // if they have plus one, they must say if they are bringing a plus one
    // if they ARE bringing a plus one, the name fields must be filled out.
    if (haveAllGuestsResponded && plusOneSectionComplete) {
      return true;
    }

    return false;
  }

  /** Returns true if any of the guests in this party have responded 'Yes' they can attend the wedding */
  get canAnyGuestAttend(): boolean {
    for (let attendChoice of this.canAttendChoice) {
      if (attendChoice == this.canAttendText_yes) {
        return true;
      }
    }
    return false;
  }

  /** Formats the date (object) of the last RSVP this guest party sent in into a nice
   * readable format string to be displayed in the UI */
  get previousRsvpResponseDateFormatted(): string {
    const date: Date = new Date(this.previousRsvpResponse!.dateCreated);
    const hours: number = date.getHours();
    let amOrPm: string;
    let hour: number;
    if (hours == 0) {
      hour = 12;
      amOrPm = "am";
    } else if (hours > 12) {
      hour = hours - 12;
      amOrPm = "pm";
    } else if (hours == 12) {
      hour = hours;
      amOrPm = "pm";
    }
    else {
      hour = hours;
      amOrPm = "am";
    }
    return date.toDateString() + ', at ' + hour + ':' + date.getMinutes() + amOrPm;
  }

  /** Returns true only if all guests in this party have responded whether they
   * can attend or not. */
  get haveAllGuestsResponded(): boolean {
    return this.canAttendChoice.filter(c => c != '').length == this.guests.length;
  }

  /** Returns true only if all guests in this party have responded and said they
   * cannot attend */
  get noGuestsCanAttend(): boolean {
    return this.canAttendChoice.filter(c => c == 'No').length == this.guests.length;
  }

  // #endregion COMPUTED


  // #region METHODS

  calcSmileFace(canAttendChoice: string): string {
    if (canAttendChoice == this.canAttendText_yes) {
      return 'sentiment_very_satisfied';
    } else if (canAttendChoice == this.canAttendText_no) {
      return 'sentiment_sad';
    } else {
      return 'sentiment_neutral';
    }
  }

  /**
   * Composes the rsvp responses from the form data, and then emits an event that the search button was clicked on
   */
  onSubmitButtonClick(): void {
    console.log('Submit button clicked');
    console.log('Creating RSVP Response...');

    try {

      // you shouldn't be able to run this function unless all guests have responded either yet or no! But just in case....
      if (!this.haveAllGuestsResponded) {
        console.error('Not all guests in party have responded');
        return;
      }

      // make list of which guests can attend, and which cant
      const guestsWhoSaidYesIds: string[] = [];
      const guestsWhoSaidNoIds: string[] = [];
      for (let guest of this.guests) {
        const canThisGuestAttend: boolean = this.canAttendChoice[this.calcGuestIndex(guest.id)] == this.canAttendText_yes;
        if (canThisGuestAttend) {
          guestsWhoSaidYesIds.push(guest.id);
        } else {
          guestsWhoSaidNoIds.push(guest.id);
        }
      }

      // add plus one data, if this guest has one and has chosen to bring one
      let plusOne: IPlusOne | null = null;
      if (this.bringingPlusOne == this.canAttendText_yes) {
        plusOne = {
          firstName: this.plusOneFirstName,
          lastName: this.plusOneLastName,
        }
      }

      // create rsvp response object
      let rsvpResponse: IRsvpResponse;
      if (this.canAnyGuestAttend) {
        rsvpResponse = createPartyYesRsvp(
          this.guestId,
          this.partyId,
          guestsWhoSaidYesIds,
          guestsWhoSaidNoIds,
          plusOne,
          this.adviceOrJokes,
          this.songRecommendations
        );
      } else {
        rsvpResponse = createPartyNoRsvp(
          this.guestId,
          this.partyId,
          guestsWhoSaidNoIds,
          this.reasonForDeclining
        );
      }

      console.log('Emitting RSVP Response...');
      this.$emit('submitButtonClicked', rsvpResponse);
    } catch (error) {
      console.error(error);
      console.log('Failed to create RSVP response');
      this.errorMessage='Failed to create RSVP response';
    }
  }

  calcGuestIndex(guestId: string): number {
    for (let i = 0; i < this.guests.length; i++) {
      if (this.guests[i].id == guestId) {
        return i;
      }
    }
    return -1;
  }

  toTitleCase = toTitleCase;

  // #endregion METHODS

  // lifecycle hooks

  async mounted(): Promise<void> {

    // get the list of guest IDs who are in this party from the database
    let guestIds: string[] | null;
    try {
      console.log('Getting IDs of all guests in this party...');
      guestIds = await getGuestIds(this.weddingDb, this.partyId);
      if (guestIds != null) {
        console.log(`Got ${guestIds.length} guest IDs for this party!`)
      } else {
        console.warn('Failed to get guests IDs for this party');
        return;
      }
    } catch (error) {
      console.warn(error);
      console.log('Failed to get guest IDs for this party');
      return;
    }

    // get the data on each guest from their guest id from the database
    try {
      console.log('Getting guest data from guest IDs...');
      this.guests = await getGuests(this.weddingDb, guestIds);
      if (this.guests != null && this.guests.length > 0) {
        console.log(`Got ${this.guests.length} guests' data for this party!`)
      } else {
        console.warn(`Failed to get guests' data!`);
      }
    } catch (error) {
      console.warn(error);
      console.log("Failed to get guests' data");
      return;
    }

    // populate response array with empty strings
    for (let i = 0; i < this.guests.length; i++) {
      this.canAttendChoice.push(''); // populate response array with empty strings
    }

    // pre-populate form with data from previous RSVP response
    if (this.previousRsvpResponse !== null) {
      try {

        console.log('Trying to import data from previous RSVP response...');

        // populate yes responses
        for (let guestId of this.previousRsvpResponse.guestsWhoSaidYesIds) {
          const guestIndex: number = this.calcGuestIndex(guestId);
          this.canAttendChoice[guestIndex] = this.canAttendText_yes;
        }

        // populate no responses
        for (let guestId of this.previousRsvpResponse.guestsWhoSaidNoIds) {
          const guestIndex: number = this.calcGuestIndex(guestId);
          this.canAttendChoice[guestIndex] = this.canAttendText_no;
        }

        // prepopulate text field questions
        const songRecommendations: string | undefined = this.previousRsvpResponse.songRecommendations;
        this.songRecommendations = songRecommendations ? songRecommendations : '';
        const adviceOrJokes: string | undefined = this.previousRsvpResponse.adviceOrJokes;
        this.adviceOrJokes = adviceOrJokes ? adviceOrJokes : '';
        const reasonForDecline: string | undefined = this.previousRsvpResponse.reasonForDecline;
        this.reasonForDeclining = reasonForDecline ? reasonForDecline : '';

        // prepopulate plus one info
        if (this.previousRsvpResponse.plusOne === null) {
          this.bringingPlusOne = this.canAttendText_no;
          this.plusOneFirstName = '';
          this.plusOneLastName = '';
        } else {
          this.bringingPlusOne = this.canAttendText_yes;
          this.plusOneFirstName = this.previousRsvpResponse.plusOne.firstName;
          this.plusOneLastName = this.previousRsvpResponse.plusOne.lastName;
        }

        console.log('Succesfully imported data from previous RSVP response.')

      } catch (error) {
        console.warn(error);
        console.log('Failed to import data from previous RSVP response');
        //todo: show in UI somehow?
      }
    }

  }

}
