
import { PropType } from 'vue';
import { Vue, Options } from 'vue-class-component';

@Options({
  props: {
    imageLinks: Array as PropType<string[]>,
    backgroundColor: String,
  },
})
export default class YourPhotos extends Vue {

  //#region PROPS

  /** The background color of the component that contains this photo viewer */
  backgroundColor!: string;

  /** An array of all image links from a google photos album to display inside
   * this component */
  imageLinks!: string[];

  /** Trur if an image is currently loading */
  loadingImage: boolean = true;

  //#endregion PROPS

  //#region DATA

  /** The index of the image that is currently being displayed in the UI */
  currentImageIndex: number = 0;

  //#endregion DATA

  //#region COMPUTED

  /** The source string for the current image to display, currently a link to a
   * photo on google photos */
  get currentImage(): string {
    this.loadingImage = true;
    return this.imageLinks[this.currentImageIndex];
  }

  //#endregion COMPUTED

  //#region METHODS

  /** Shows the next image in the UI */
  nextImage(): void {
    if (this.currentImageIndex < this.imageLinks.length - 1) {
      this.currentImageIndex++;
    } else {
      this.currentImageIndex = 0;
    }
  }

  /** Shows the previous image in the UI */
  previousImage(): void {
    if (this.currentImageIndex > 0) {
      this.currentImageIndex--;
    } else {
      this.currentImageIndex = this.imageLinks.length - 1;
    }
  }

  onLoad(): void {
    this.loadingImage = false;
  }

  //#endregion METHODS

  //#region LIFECYCLE HOOKS

  mounted() {
  }

  //#endregion LIFECYCLE HOOKS

}
