
import { initializeWeddingRsvpFirebaseApp } from '@/utilities/FirebaseUtils';
import { FirebaseApp } from 'firebase/app';
import { Vue } from 'vue-class-component';
import { FirebaseStorage, getStorage, ref, uploadBytes } from "firebase/storage";
import { UserCredential, getAuth, signInAnonymously } from 'firebase/auth';

//todo: clean and comment entire file!!!!
export default class PhotoUpload extends Vue {

  //#region DATA
  uploadStatus: string = '';
  storage: FirebaseStorage | null = null;
  auth: any;
  selectedFile: any = null;
  selectedFileText: string = '';
  selectedFileTextColor: string = 'black';
  creds: UserCredential | null = null;
  //#endregion DATA

  //#region METHODS

  handleFileInput(event: any) {
    const file = event.target.files[0];
    this.processFile(file);
  }
  handleFileDrop(event: any) {
    event.preventDefault();
    const file = event.dataTransfer.files[0];
    this.processFile(file);
  }
  processFile(file: any) {
    if (this.isFileSmallEnough(file)) {
      this.selectedFile = file;
      this.selectedFileText = this.selectedFile.name;
      this.selectedFileTextColor = 'black';
    } else {
      this.selectedFileText = 'Selected file is larger than 5MB';
      this.selectedFileTextColor = 'red';
    }
  }
  isFileSmallEnough(file: any): boolean {
    const sizeInMb: number = file.size / (1024 * 1024); // converts bytes to MB
    if (sizeInMb < 5.0) {
      return true;
    } else {
      return false;
    }
  }
  async uploadFile() {
    try {
      console.log('Trying to upload selected file...');

      // create name for uploaded image
      const nameForImage: string = `${Date.now()}_${this.creds!.user.uid.substring(0, 4)}_${this.selectedFile.name}`;
      const imgRef = ref(this.storage!, nameForImage);
      const result: any = await uploadBytes(imgRef, this.selectedFile);
      //todo: check result to see if file upload worked, and maybe get smarter error if it didn't

      console.log('Photo uploaded successfully!');
      this.uploadStatus = `Your photo '${this.selectedFile.name}' has been uploaded!`;

      // clear our previously selected file
      this.selectedFile = null;
      this.selectedFileText = '';
    } catch (error) {
      console.warn(error);
      console.log('Failed to upload selected file');
      this.uploadStatus = "Upload failed";
    }
  }

  //#endregion METHODS

  //#region LIFECYCLE HOOKS

  async mounted() {

    //todo: add debug comments?
    //todo: limit upload file number and size

    // Initialize Firebase
    const firebaseApp: FirebaseApp | null = initializeWeddingRsvpFirebaseApp();

    // Initialize Cloud Storage and get a reference to the service
    this.storage = getStorage(firebaseApp!);

    this.auth = getAuth(firebaseApp!);

    this.creds = await signInAnonymously(this.auth);

  }

  //#endregion LIFECYCLE HOOKS

}
