
import { Options, Vue } from 'vue-class-component';

@Options({
  props: {
    errorMessage: {
      required: true,
      type: String,
    },
    searchButtonDisabledExternally: {
      required: true,
      type: Boolean,
    },
  },
})
export default class HomeView extends Vue {

  // props

  /** Error message to display if a search for a guest failed */
  errorMessage!: string;

  /** True when the parent component wants the search button to be disabled */
  searchButtonDisabledExternally!: boolean;

  // data

  /** The first name of the guest to search for */
  firstName: string = '';

  /** The last name of the guest to search for */
  lastName: string = '';

  /** The email name of the guest to search for */
  email: string = '';

  /** The number of times the search button has been clicked during this session
   * */
  searches: number = 0;

  // computed

  /** True only if the search button should currently be disabled */
  get searchButtonDisabled(): boolean {
    return this.firstName.length === 0 || this.lastName.length === 0 || this.email.length === 0 || this.searchButtonDisabledExternally;
  }

  // methods

  /** clears all data from the input form fields */
  clearForm(): void {
    this.firstName = '';
    this.lastName = '';
    this.email = '';
  }

  /**
   * Emits an event that the search button was clicked on
   */
  onSearchButtonClick(): void {
    this.searches++;
    this.$emit('searchButtonClicked', this.firstName, this.lastName, this.email);
  }

}
