  /**
   * Tries to find the guest party that contains a guest matching the given data
   * @param firstName the guest's first name to search for
   * @param lastName the guest's last name to search for
   * @param email the guest's email address to search for
   */
  async getGuestParty(firstName: string, lastName: string, email: string): Promise<void> {

    console.log('Searching for guest party...');

    // disable search button to prevent multiple searches
    this.disableSearchButton = true;

    // get guest id from guest info
    try {
      console.log('Searching for matching guest...');
      const guestId: string | null = await getGuestId(this.database!, firstName, lastName, email);
      if (guestId != null) {
        this.currentGuestId = guestId;
        console.log("Guest found!");
      } else {
        console.log('No matching guest found');
        this.disableSearchButton = false;
        this.searchErrorMessage = 'Guest not found';
        return;
      }
    } catch (error) {
      console.warn(error);
      console.log('Failed to find matching guest!');
      this.disableSearchButton = false;
      return;
    }

    // get party id
    try {
      console.log("Searching for guest's party...");
      const partyId: string | null = await getPartyId(this.database!, this.currentGuestId);
      if (partyId != null) {
        this.currentGuestPartyId = partyId;
        console.log("Party found!");
      } else {
        console.log('No matching party found');
        this.disableSearchButton = false;
        this.searchErrorMessage = "Guest's party not found";
        return;
      }
    } catch (error) {
      console.warn(error);
      console.log("Failed to find guest's party!");
      this.disableSearchButton = false;
      return;
    }

    // get latest RSVP responses for this party, if one exists
    try {
      console.log('Searching for previous RSVPs...');
      const previousRsvp: IRsvpResponse | null = await getLatestRsvp(this.database!, this.currentGuestPartyId);
      if (previousRsvp != null) {
        this.existingRsvpResponse = previousRsvp;
        console.log("Previous RSVP found!");
      } else {
        console.log('No previous RSVP found');
      }
    } catch (error) {
      console.warn(error);
      console.log('Failed to check for previous RSVPs');
      this.disableSearchButton = false;
    }

    // clear form data
    (this.$refs.rsvpSearchForm as any).clearForm();

    // display RSVP resposne form for the found guest party
    this.showRsvpResponseForm = true;

    // re-enable search button
    this.disableSearchButton = false;

  }

  //todo: combine shared/repeated logic with that from above method
  /**
 * Tries to find the guest party that contains a guest matching the given data
 * @param guestId ID of the guest who's party to search for
 */
  async getGuestPartyFromId(guestId: string): Promise<void> {

    console.log('Searching for guest party...');

    // disable search button to prevent multiple searches
    this.disableSearchButton = true;

    this.currentGuestId = guestId;

    // get party id
    try {
      console.log("Searching for guest's party...");
      const partyId: string | null = await getPartyId(this.database!, this.currentGuestId);
      if (partyId != null) {
        this.currentGuestPartyId = partyId;
        console.log("Party found!");
      } else {
        console.log('No matching party found');
        this.disableSearchButton = false;
        this.searchErrorMessage = "Guest's party not found";
        return;
      }
    } catch (error) {
      console.warn(error);
      console.log("Failed to find guest's party!");
      this.disableSearchButton = false;
      return;
    }

    // get latest RSVP responses for this party, if one exists
    try {
      console.log('Searching for previous RSVPs...');
      const previousRsvp: IRsvpResponse | null = await getLatestRsvp(this.database!, this.currentGuestPartyId);
      if (previousRsvp != null) {
        this.existingRsvpResponse = previousRsvp;
        console.log("Previous RSVP found!");
      } else {
        console.log('No previous RSVP found');
      }
    } catch (error) {
      console.warn(error);
      console.log('Failed to check for previous RSVPs');
      this.disableSearchButton = false;
    }

    // clear form data
    (this.$refs.rsvpSearchForm as any).clearForm();

    // display RSVP resposne form for the found guest party
    this.showRsvpResponseForm = true;

    // re-enable search button
    this.disableSearchButton = false;

  }

  /** Returns true if the given strings match when ignoring both letter case and
   * any whtie space in either string */
  compareStringsIgnoreCaseAndWhiteSpace(string1: string, string2: string): boolean {
    return string1.toLowerCase().replace(/\s/g, '') === string2.toLowerCase().replace(/\s/g, '');
  }

  async postRsvpResponses(rsvpResponse: IRsvpResponse): Promise<void> {
    console.log(`Trying to save RSVP response...`);

    const database: Firestore = this.database!;
    const collectionName: string = 'rsvps';
    const time: number = Date.now();

    // compose rsvp object for database
    const documentName: string = `${time}_${rsvpResponse.partyId}`; //? also document id/name? using ms since epoch for now, so they are ordered in the order they come in
    const docRef: DocumentReference<DocumentData> = doc(database, collectionName, documentName);

    // add extra property if not in prod, so I can filter out the posts from dev mode later
    if (process.env.NODE_ENV != 'production') {
      rsvpResponse.devMode = true;
    }

    // try to post document to database
    try {
      await setDoc(docRef, rsvpResponse);
      console.log('saved RSVP response to database!');
      this.showRsvpConfirmationModal = true;
    } catch (error) {
      console.log('Failed to save RSVP response: ' + error);
      this.rsvpResposneFormErrorMessage = "Failed to send RSVP";
    }

  }

  //#endregion METHODS


  // lifecycle hooks

  async mounted(): Promise<void> {

    // get wedding-rsvp-app firebase app
    this.firebaseApp = initializeWeddingRsvpFirebaseApp();

    // get wedding-rsvp firestore database
    this.database = getFirestoreDatabase();

    // get firebase auth
    this.auth = getAuth(this.firebaseApp!);
    console.log(this.auth);
    this.userCredentials = await signInAnonymously(this.auth);
    console.log(this.userCredentials);

    // check for data in url
    try {
      console.log('checking URL for search parameters...');
      const params = new Proxy(new URLSearchParams(window.location.search), {
        get: (searchParams, prop) => searchParams.get(prop as string),
      });
      // Get the value of "some_key" in eg "https://example.com/?some_key=some_value"
      const guestId: string = (params as any).guestId;
      console.log(guestId);
      if (guestId != null && guestId != '') {
        console.log('Found URL params, searching for Guest Party...');
        await this.getGuestPartyFromId(guestId);
      } else {
        console.log('required URL search parameters not found.');
      }
    } catch (error) {
      console.warn(error);
      console.log('Error parsingURL search parameters.');
    }
  }

